<template>
	<div>
		<heading title="" />
		<div style="height: 80px;"></div>
		<mu-container>
			<mu-form ref="form" :model="validateForm" class="mu-demo-form">
				<mu-form-item label="" help-text="" prop="username" :rules="usernameRules">
					<mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
				</mu-form-item>
				<mu-form-item label="" prop="password" :rules="passwordRules">
					<mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
				</mu-form-item>
				<mu-form-item prop="date" label="">
					<mu-date-input v-model="validateForm.date" type="dateTime" actions></mu-date-input>
				</mu-form-item>
				<mu-form-item prop="isAgree" :rules="argeeRules">
					<mu-checkbox label="" v-model="validateForm.isAgree"></mu-checkbox>
				</mu-form-item>
				<mu-form-item style="margin-left: 40%;margin-top: 80px;">
					<mu-button color="primary" @click="submit"></mu-button>
					<mu-button @click="clear"></mu-button>
				</mu-form-item>
			</mu-form>
		</mu-container>
	</div>
</template>

<script>
	import heading from '@/components/head.vue'
	export default {
		data() {
			return {
				usernameRules: [{
						validate: (val) => !!val,
						message: ''
					},
					{
						validate: (val) => val.length >= 3,
						message: '3'
					}
				],
				passwordRules: [{
						validate: (val) => !!val,
						message: ''
					},
					{
						validate: (val) => val.length >= 3 && val.length <= 10,
						message: '310'
					}
				],
				argeeRules: [{
					validate: (val) => !!val,
					message: ''
				}],
				validateForm: {
					username: '',
					password: '',
					isAgree: false,
					date:'',
				}
			}
		},

		components: {
			heading,
		},
		mounted() {

		},
		created() {

		},
		methods: {
			submit() {
				this.$refs.form.validate().then((result) => {
					if (result) {
					  this.$toast.success('');
					} else{
					this.$toast.error('');
					}
					console.log('form valid: ', result)
				});
			},
			clear() {
				this.$refs.form.clear();
				this.validateForm = {
					username: '',
					password: '',
					isAgree: false
				};
			}
		}

	}
</script>

<style scoped="scoped">
@import '//fonts.useso.com/css?family=Roboto:300,400,500,700,400italic';
@import 'http://cdn.bootcss.com/material-design-icons/3.0.1/iconfont/material-icons.css';

</style>